import React from 'react'

export default function cardTree(props) {
    return (
        <div className="card ">
            <div className="img">
                <div>
                    {/* <img className="imgCard" src={props.data.Imagen} alt="" /> */}
                        <div className="textCard">
                            <p class="titleCard"> {props.data.nombre}</p>
                            <div className="displayCard">
                                <p> {props.data.descripcion} </p>
                            </div>
                        </div>
                        <a className="titleCardd  btn btn-primary" href={props.data.src}><i class="fas fa-clone left"></i> Mirar proyecto</a>
                </div>
            </div>
        </div>
    )
}
