import React from 'react'
import Carousel from 'react-bootstrap/Carousel';

export default function carrucel() {
    return (
        <div>
            <Carousel className="carrucelData">

                <Carousel.Item>
                    <section id="carrucelImgCard">
                    </section>
                    <Carousel.Caption className="datosPersonales">
                        <h3 className="letra">Silvio Andres Cuello</h3>
                        <p className="letro">Programador de la provincia de Tucuman, Argentina.</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <section id="carrucelImgCardd">
                    </section>
                    <Carousel.Caption className="datosPersonales">
                        <h3 className="letra">Informatica</h3>
                        <p className="letro">En esta pagina planeo subir todo el contenido que aprenda agregando todos los proyectos que me proponga.</p>
                    </Carousel.Caption>
                </Carousel.Item>

            </Carousel>
        </div>
    )
}
